<template>
  <div class="area-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
var echarts = require("echarts");
export default {
  components: {},
  props: ["chartId", "datas", "config"],
  name: "annularChart",
  data() {
    return {
      legend: []
    };
  },
  methods: {
    ...mapActions([]),
    setChart(datas) {
      console.log("annularChart", this.chartId);

      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.chartId));
      let option = {
        tooltip: {
          trigger: "item",
          textStyle: {
            fontSize: 12 * this.fontScale
          },
          formatter: "{b}: {d}%"
        },
        color: this.config.color ? this.config.color : [],
        legend: {
          orient: "vertical",
          x: "60%",
          y: "center",
          itemGap: 6 * this.fontScale,
          itemHeight: 6 * this.fontScale,
          itemWidth: 10 * this.fontScale,
          textStyle: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          data: this.legend
        },
        series: [
          {
            type: "pie",
            hoverOffset: 3 * this.fontScale,
            center: ["30%", "50%"],
            radius: ["65%", "80%"],
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: 14 * this.fontScale,
                  fontWeight: "bold"
                }
              }
            },
            data: datas
          }
        ]
      };
      myChart.setOption(option);
    }
  },
  watch: {
    datas(newVal) {
      this.setChart(newVal);
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {},
  mounted() {
    this.legend = this.datas.filter(item => {
      return item.name;
    });
    this.setChart(this.datas);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.area-wrap {
  width: 100%;
  height: 100%;
}
</style>