<template>
  <div class="map-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
var echarts = require("echarts");
export default {
  components: {},
  name: "barChart",
  data() {
    return {
      data: []
    };
  },
  props: ["barChart", "chartId"],
  methods: {
    ...mapActions([]),
    setChart(datas) {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.chartId));
      let barChart = {
        legend: {
          top: 0,
          right: "3%",
          itemHeight: 6 * this.fontScale,
          itemWidth: 10 * this.fontScale,
          textStyle: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          type: 'scroll',
          data: ['历史总数', '当前数据']
        },
        tooltip: {
          trigger: "axis",
          show: true,
          textStyle: {
            fontSize: 12 * this.fontScale
          },
          formatter: function(params) {
            console.log(params[0].data);

            let str =
              params[0].data[0] +
              "<br>" +
              "共计：" +
              params[0].data[1] +
              "<br>" +
              "历史总数：" +
              params[0].data[2] +
              "<br>" +
              "当前数据：" +
              params[0].data[3];
            return str;
          }
        },
        dataset: {
          source: datas.data
        },
        grid: {
          width: "85%",
          height: "55%",
          bottom: "20%"
        },
        xAxis: {
          type: "category",
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#21628c"
            }
          },
          axisPointer: {
            type: "shadow"
          }
        },
        yAxis: {
          name: "{a|" + datas.yAxis + "}",
          nameGap: 30,
          nameTextStyle: {
            rich: {
              a: {
                color: "#90a9ce",
                align: "right",
                width: "100%",
                fontSize: 12 * this.fontScale
              }
            }
          },
          type: "value",
          axisLine: {
            show: false
          },
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          axisTick: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: "#13325b"
            }
          }
        },
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [
          {
            type: "line",
            showSymbol: false,
            smooth: true,
            symbol: "circle",
            legendHoverLink: {
              show: false
            },
            markPoint: {
              symbol: "roundRect",
            },
            lineStyle: {
              color: "#1EA6AF",
              width: 1
            },
            itemStyle: {
              color: "#1EA6AF",
              fontSize: 12 * this.fontScale
            },
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(30, 166, 175, .4)" // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(31, 31, 122, .4)" // 100% 处的颜色
                  }
                ],
                global: false // 缺省为 false
              }
            }
          },
          {
            type: "bar",
            barWidth: "20%",
            itemStyle: {
              normal: {
                color: "#36DEC1"
              }
            }
          },
          {
            type: "bar",
            barWidth: "20%",
            itemStyle: {
              normal: {
                color: "#3A78F5"
              }
            }
          }
        ]
      };
      myChart.setOption(barChart);
    }
  },
  watch: {
    linebarChart: function(newVal) {
      this.setChart(newVal);
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {},
  mounted() {
    this.setChart(this.barChart);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.map-wrap {
  width: 100%;
  height: 100%;
}
</style>

