<template>
  <div class="radar-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import radar from "echarts";

export default {
  components: {},
  props: ["chartId", "dataList", "radarIndicator", "leType"],
  name: "radar-echart",
  data() {
    return {
      data: [],
      color: [
        { line: "#00F6D7", area: "rgba(0, 229, 200, 0.36)" },
        { line: "#04B2FF", area: "rgba(36, 210, 255, 0.51)" }
      ]
    };
  },
  methods: {
    ...mapActions([]),
    setChart() {
      this.data = this.dataList;
      let arr = [];
      if (this.data.length > 1) {
        for (let i = 0; i < this.data.length; i++) {
          let obj = {
            type: "radar",
            name: this.data[i].name,
            data: [this.data[i]],
            symbol: "none",
            areaStyle: {
              color: this.color[i].area
            },
            lineStyle: {
              width: 1,
              color: this.color[i].line
            },
            color: this.color[i].line
          };
          arr.push(obj);
        }
        console.log(arr);
      } else {
        let obj = {
          type: "radar",
          data: this.data,
          symbol: "none",
          areaStyle: {
            color: "rgba(0, 229, 200, 0.36)"
          },
          itemStyle: {
            color: "#24E8FF"
          },
          lineStyle: {
            width: 1,
            color: "#24E8FF"
          }
        };
        arr.push(obj);
      }
      this.data[0].areaStyle = {
        normal: {
          opacity: 0.4,
          color: "#24E8FF"
        }
      };
      // console.log(this.data);
      let myChart = radar.init(document.getElementById(this.chartId));
      let right = this.leType ? "center" : "3%";
      let bottom = this.leType ? "-3%" : 8 * this.fontScale;
      let option = {
        legend: {
          bottom: bottom,
          right: right,
          itemHeight: 6 * this.fontScale,
          itemWidth: 10 * this.fontScale,
          icon: "roundRect",
          textStyle: {
            color: "#90A9CE",
            fontSize: 12 * this.fontScale
          }
        },
        radar: {
          name: {
            textStyle: {
              color: "#CCD8E9",
              borderRadius: 3,
              fontSize: 12 * this.fontScale,
              fontWeight: "light",
              padding: [-3 * this.fontScale, -3 * this.fontScale]
            }
          },
          center: ["50%", "50%"],
          radius: "70%",
          shape: "circle",
          indicator: this.radarIndicator,
          axisLine: {
            lineStyle: {
              type: "solid",
              color: "rgba(137, 175, 197, 0.33)"
            }
          },
          splitLine: {
            lineStyle: {
              type: "dashed",
              color: "rgba(137, 175, 197, 0.33)"
            }
          },
          splitArea: {
            areaStyle: {
              color: "transparent"
            }
          }
        },
        series: arr
      };
      myChart.setOption(option);

      //建议加上以下这一行代码，不加的效果图如下（当浏览器窗口缩小的时候）。超过了div的界限（红色边框）
      window.addEventListener("resize", function() {
        myChart.resize();
      });
    }
  },
  watch: {
    // chartData(newVal) {}
    dataList(newVal) {
      this.setChart();
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {},
  mounted() {
    this.setChart();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.radar-wrap {
  width: 100%;
  height: 100%;
}
</style>