<template>
  <div class="area-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
var echarts = require("echarts");
export default {
  components: {},
  props: ["chartId", "areaData"],
  name: "areaChart",
  data() {
    return {};
  },
  methods: {
    ...mapActions([]),
    setChart(datas) {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.chartId));
      let option = {
        tooltip: {
          trigger: "axis",
          textStyle: {
            fontSize: 12 * this.fontScale
          },
          formatter: function(params) {
            return params[0].axisValue + "年\n参与总人数：" + params[0].data;
          }
        },
        dataset: {
          source: datas
        },
        xAxis: {
          type: "category",
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#90A9CE"
            }
          }
        },
        grid: {
          width: "85%",
          height: "70%",
          bottom: "20%"
        },
        yAxis: {
          type: "value",
          axisLine: {
            show: false
          },
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          axisTick: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: "#13325b"
            }
          }
        },
        series: [
          {
            type: "line",
            showSymbol: false,
            smooth: true,
            symbol: "circle",
            lineStyle: {
              color: "#1EA6AF",
              width: 1
            },
            itemStyle: {
              color: "#1EA6AF",
              fontSize: 12 * this.fontScale
            },
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(30, 166, 175, .4)" // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(31, 31, 122, .4)" // 100% 处的颜色
                  }
                ],
                global: false // 缺省为 false
              }
            }
          }
        ]
      };
      myChart.setOption(option);
    }
  },
  watch: {
    areaData(newVal) {
      this.setChart(newVal);
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {},
  mounted() {
    this.setChart(this.areaData);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.area-wrap {
  width: 100%;
  height: 100%;
}
</style>