<template>
  <div class="map-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
var echarts = require("echarts");
export default {
  components: {},
  name: "GRBarChart",
  data() {
    return {};
  },
  props: ["chartId", "GRBarChart", "config"],
  methods: {
    ...mapActions([]),
    setChart(datas) {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.chartId));
      let barChart = {
        legend: {
          bottom: "0",
          right: "center",
          itemHeight: 6 * this.fontScale,
          itemWidth: 10 * this.fontScale,
          textStyle: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          }
        },
        tooltip: {
          show: true,
          textStyle: {
            fontSize: 12 * this.fontScale
          },
          formatter: function(params) {
            let str =
              params.data[0] +
              "<br>" +
              "历史总数：" +
              params.data[1] +
              "<br>" +
              "当前数据：" +
              params.data[2];
            return str;
          }
        },
        dataset: {
          source: datas
        },
        grid: {
          width: "85%",
          height: "55%",
          bottom: "25%"
        },
        xAxis: {
          type: "category",
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#21628c"
            }
          },
          axisPointer: {
            type: "shadow"
          }
        },
        yAxis: {
          name: "{a|" + (this.config ? this.config.yAxis : "") + "}",
          nameGap: 15 * this.fontScale,
          nameTextStyle: {
            rich: {
              a: {
                color: "#90a9ce",
                align: "right",
                width: "100%",
                fontSize: 12 * this.fontScale
              }
            }
          },
          type: "value",
          axisLine: {
            show: false
          },
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          axisTick: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: "#13325b"
            }
          }
        },
        series: [
          {
            type: "bar",
            barWidth: "15%",
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#45f0aa" },
                  { offset: 1, color: "#37adf0" }
                ])
              }
            }
          },
          {
            type: "bar",
            barWidth: "15%",
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#F5C33A" },
                  { offset: 1, color: "#DF5E59" }
                ])
              }
            }
          }
        ]
      };
      myChart.setOption(barChart);
    }
  },
  watch: {
    linebarChart: function(newVal) {
      this.setChart(newVal);
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {},
  mounted() {
    this.setChart(this.GRBarChart);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.map-wrap {
  width: 100%;
  height: 100%;
}
</style>

