<template>
  <div class="header-date-wrap">
    <div class="today-state">
      <div class="today-detail today-day">{{day}}</div>
      <div class="today-detail today-time">{{time}}</div>
      <div class="today-detail today-week">{{week}}</div>
      <div class="today-detail today-weather">{{currentWeather}}</div>
      <!-- <div class="today-weather">{{temperature}}℃</div> -->
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  components: {
  },
  name: "headerDate",
  data() {
    return {
      weatherObj: null,
      currentWeather: '',
      temperature: 0,
      day: '',
      time: '',
      week: '',
    }
  },
  methods: {
    ...mapActions([])
  },
  watch: {
  },
  computed: {
    ...mapGetters([])
  },
  created() {
    let that = this;
    AMap.plugin('AMap.Weather', function () {
      let weather = new AMap.Weather();
      weather.getLive('上海市', function (err, data) {
        that.temperature = data.temperature;
        that.currentWeather = data.weather;
      });
      setInterval(() => {
        weather.getLive('上海市', function (err, data) {
          that.temperature = data.temperature;
          that.currentWeather = data.weather;
        });
      }, 60000);
    })
    let date = new Date();
    this.day = date.getFullYear() + '-' + setTime(date.getMonth() + 1) + '-' + setTime(date.getDate());
    this.time = date.getHours() + ':' + setTime(date.getMinutes()) + ':' + setTime(date.getSeconds());
    function setTime(num) {
      if (num < 10) {
        num = '0' + num;
      }
      return num;
    }
    function setweek(weekNum) {
      let week = ''
      switch (date.getDay()) {
        case 0:
          week = '星期天'
          break;
        case 1:
          week = '星期一'
          break;
        case 2:
          week = '星期二'
          break;
        case 3:
          week = '星期三'
          break;
        case 4:
          week = '星期四'
          break;
        case 5:
          week = '星期五'
          break;
        case 6:
          week = '星期六'
          break;
      }
      return week;
    }
    this.week = setweek(date.getDay());
    setInterval(() => {
      let date = new Date();
      this.time = date.getHours() + ':' + setTime(date.getMinutes()) + ':' + setTime(date.getSeconds());
      if (this.time === '00:00:00') {
        this.week = setweek(date.getDay());
        this.day = date.getFullYear() + '-' + setTime(date.getMonth() + 1) + '-' + setTime(date.getDate());
      }
    }, 1000);
  },
  mounted() {
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.header-date-wrap {
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: .8rem;
  .today-state {
    display: flex;
    width: 100%;
    padding-right: .2rem;
    padding-top: .35rem;
    justify-content: flex-end;
  }
  .today-detail {
    line-height: .45rem;
    font-size: .16rem;
    color: #CCD8E9;
  }
  .today-day {
    padding-right: .2rem;
  }
  .today-week,
  .today-time {
    padding-right: .15rem;
  }
}
</style>