<template>
  <div class="map-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
var echarts = require("echarts");
export default {
  components: {},
  name: "curveareaChart",
  data() {
    return {
      dataMap: {
        all: null,
        charts: {}
      }
    };
  },
  props: ["chartId", "curveareaData", "icons"],
  methods: {
    ...mapActions([]),
    setChart(datas) {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.chartId));
      let that = this;
      let option = {
        legend: {
          data: [
            { name: "浏览人数", icon: "roundRect" },
            { name: "参与人数", icon: "roundRect" }
          ],
          top: 0,
          right: "3%",
          itemHeight: 6 * this.fontScale,
          itemWidth: 10 * this.fontScale,
          textStyle: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          }
        },
        tooltip: {
          trigger: "axis",
          textStyle: {
            fontSize: 12 * this.fontScale
          }
        },
        dataset: {
          source: datas
        },
        xAxis: {
          type: "category",
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#21628c",
              width: 1
            }
          },
          axisPointer: {
            type: "shadow"
          }
        },
        yAxis: {
          type: "value",
          axisLine: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: "#13325b",
              width: 1
            }
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          }
        },
        grid: {
          top: "20%",
          width: "85%",
          bottom: "20%"
        },
        series: [
          {
            type: "line",
            smooth: true,
            showSymbol: false,
            symbol: "circle",
            itemStyle: {
              color: "#7A48F1"
            },
            lineStyle: {
              color: "#7A48F1",
              width: 1
            },
            areaStyle: {
              color: {
                type: "linear",
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(14,66,223,0.17)" // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(85,61,164,0.17)" // 100% 处的颜色
                  }
                ]
              }
            }
          },
          {
            type: "line",
            smooth: true,
            showSymbol: false,
            symbol: "circle",
            itemStyle: {
              color: "#00FFFF"
            },
            lineStyle: {
              color: "#00FFFF",
              width: 1
            },
            areaStyle: {
              color: {
                type: "linear",
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(30,166,175,0.2)" // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(31,31,122,0.2)" // 100% 处的颜色
                  }
                ]
              }
            }
          }
        ]
      };
      myChart.setOption(option);
    }
  },
  watch: {
    curveareaData: function(newVal) {
      this.setChart(newVal);
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {},
  mounted() {
    this.setChart(this.curveareaData);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";

.map-wrap {
  width: 100%;
  height: 100%;
}
</style>

