<template>
  <div class="line-three-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
var echarts = require("echarts");
export default {
  components: {},
  props: ["chartId", "chartData", "lineThreecolors", "linethreeConfig"],
  name: "lineThreeChart",
  data() {
    return {
      data: [
        ["名称", "项目总数", "临期", "超期"],
        ["2014", 220, 100, 30],
        ["2015", 300, 170, 50],
        ["2016", 320, 80, 70],
        ["2017", 180, 140, 50],
        ["2018", 230, 80, 60],
        ["2019", 180, 70, 30]
      ]
    };
  },
  methods: {
    ...mapActions([]),
    setChart() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.chartId));
      let option = {
        title: false,
        tooltip: {
          trigger: "axis",
          textStyle: {
            fontSize: 12 * this.fontScale
          }
        },
        legend: {
          top: 8 * this.fontScale,
          right: "3%",
          itemHeight: 6 * this.fontScale,
          itemWidth: 10 * this.fontScale,
          icon: "roundRect",
          textStyle: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          }
        },
        dataset: {
          source: this.data
        },
        grid: {
          width: "85%",
          height: "55%",
          bottom: "20%"
        },
        xAxis: {
          type: "category",
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#21628c"
            }
          }
        },
        yAxis: {
          type: "value",
          name:
            "{a|" + (this.linethreeConfig ? this.linethreeConfig.unit : "") + "}",
          nameGap: 20,
          nameTextStyle: {
            rich: {
              a: {
                color: "#90a9ce",
                align: "right",
                width: "100%",
                fontSize: 12 * this.fontScale
              }
            }
          },
          axisLine: {
            show: false
          },
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          axisTick: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: "#13325b"
            }
          }
        },
        series: [
          {
            type: "line",
            showSymbol: false,
            itemStyle: {
              color: this.lineThreecolors[0]
            }
          },
          {
            type: "line",
            showSymbol: false,
            itemStyle: {
              color: this.lineThreecolors[1]
            }
          },
          {
            type: "line",
            showSymbol: false,
            itemStyle: {
              color: this.lineThreecolors[2]
            }
          }
        ]
      };
      myChart.setOption(option);
    }
  },
  watch: {
    chartData(newVal) {
      this.data = newVal;
      this.setChart();
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {},
  mounted() {
    this.data = this.chartData;
    this.setChart();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.line-three-wrap {
  width: 100%;
  height: 100%;
}
</style>