<template>
  <div class="map-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
var echarts = require("echarts");
export default {
  components: {},
  name: "linebarChart",
  data() {
    return {
      data: []
    };
  },
  props: ["linebarChart", "chartId"],
  methods: {
    ...mapActions([]),
    setChart(datas) {
      console.log(datas);
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.chartId));
      let linebarChart = {
        legend: {
          data: [
            { name: datas.data[0].name, icon: "roundRect" },
            { name: datas.data[1].name, icon: "roundRect" }
          ],
          itemHeight: 6 * this.fontScale,
          itemWidth: 10 * this.fontScale,
          top: 0,
          right: 0,
          textStyle: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          }
        },
        tooltip: {
          trigger: "axis",
          padding: [12, 9],
          textStyle: {
            color: "#90a9ce",
            lineHeight: 32,
            fontSize: 12 * this.fontScale
          },
          formatter: function(params) {
            return <p>params[0].color</p>;
          }
        },
        grid: {
          left:'15%',
          top: "20%",
          bottom: "15%"
        },
        xAxis: [
          {
            type: "category",
            axisLabel: {
              color: "#90a9ce",
              fontSize: 12 * this.fontScale
            },
            splitArea: {
              interval: 5
            },
            axisTick: {
              show: false
            },
            axisLine: {
              lineStyle: {
                color: "#21628c"
              }
            },
            data: datas.xAxis,
            axisPointer: {
              type: "shadow"
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                color: "#13325b"
              }
            },
            axisTick: {
              show: false
            },
            min: 0,
            max: 100,
            interval: 25,
            axisLabel: {
              formatter: "{value}%",
              color: "#90a9ce",
              fontSize: 12 * this.fontScale
            }
          }
        ],
        series: [
          {
            name: datas.data[0].name,
            type: "bar",
            barWidth: "20%",
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#45f0aa" },
                  { offset: 1, color: "#37adf0" }
                ])
              }
            },
            data: datas.data[0].data
          },
          {
            name: datas.data[1].name,
            type: "line",
            showSymbol: false,
            symbol: "circle",
            itemStyle: {
              color: "#aa68fe",
              borderColor: "#fff",
              borderWidth: 2 * this.fontScale
            },
            data: datas.data[1].data
          }
        ]
      };

      myChart.setOption(linebarChart);
    }
  },
  watch: {
    linebarChart: function(newVal) {
      this.setChart(newVal);
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {},
  mounted() {
    this.setChart(this.linebarChart);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";

.map-wrap {
  width: 100%;
  height: 100%;
}
</style>

