<template>
  <div class="word-wrap">
    <div id="tagbox">
      <a
        class="word-link"
        :class="item.class"
        href="JavaScript:;"
        v-for="(item,index) in words"
        :key="index"
      >{{item.data}}</a>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  components: {
  },
  props: ['wordsContent'],
  name: 'wordCloud',
  data() {
    return {
      oDiv: null,
      aA: null,
      radius: 0,
      padding: 160,
      d: 200,
      mcList: [],
      active: false,
      lasta: 1,
      lastb: 1,
      distr: true,
      tspeed: 5,
      size: 250,
      mouseX: 0,
      mouseY: 0,
      howElliptical: 1,
      i: 0,
      oTag: null,
      sa: null,
      ca: null,
      sb: null,
      cb: null,
      sc: null,
      cc: null,
      words: [],
      colorName: ['color-blue', 'color-red', 'color-green', 'color-yellow']
    }
  },
  methods: {
    ...mapActions([
    ]),
    setCloud() {
      let that = this;
      // 显示圆球半径：外盒子减去边距/2
      this.radius = this.oDiv.offsetWidth > this.oDiv.offsetHeight ? (this.oDiv.offsetHeight - this.padding) / 2 : (this.oDiv.offsetWidth - this.padding) / 2;
      for (let i = 0; i < this.aA.length; i++) {
        this.oTag = {};
        this.oTag.offsetWidth = this.aA[i].offsetWidth;
        this.oTag.offsetHeight = this.aA[i].offsetHeight;
        this.mcList.push(this.oTag);
      }
      this.sineCosine(0, 0, 0);
      this.positionAll();
      this.oDiv.onmouseover = function () {
        that.active = true;
      };
      this.oDiv.onmouseout = function () {
        that.active = false;
      };
      this.oDiv.onmousemove = function (ev) {
        var oEvent = window.event || ev;

        that.mouseX = oEvent.clientX - (that.oDiv.offsetLeft + that.oDiv.offsetWidth / 2);
        that.mouseY = oEvent.clientY - (that.oDiv.offsetTop + that.oDiv.offsetHeight / 2);

        that.mouseX /= 5;
        that.mouseY /= 5;
      };
      setInterval(this.update, 30);
    },
    update() {
      var a;
      var b;

      if (this.active) {
        a = (-Math.min(Math.max(-this.mouseY, -this.size), this.size) / this.radius) * this.tspeed;
        b = (Math.min(Math.max(-this.mouseX, -this.size), this.size) / this.radius) * this.tspeed;
      }
      else {
        a = this.lasta * 0.98;
        b = this.lastb * 0.98;
      }

      this.lasta = a;
      this.lastb = b;

      if (Math.abs(a) <= 0.01 && Math.abs(b) <= 0.01) {
        return;
      }

      var c = 0;
      this.sineCosine(a, b, c);
      for (var j = 0; j < this.mcList.length; j++) {
        var rx1 = this.mcList[j].cx;
        var ry1 = this.mcList[j].cy * this.ca + this.mcList[j].cz * (-this.sa);
        var rz1 = this.mcList[j].cy * this.sa + this.mcList[j].cz * this.ca;

        var rx2 = rx1 * this.cb + rz1 * this.sb;
        var ry2 = ry1;
        var rz2 = rx1 * (-this.sb) + rz1 * this.cb;

        var rx3 = rx2 * this.cc + ry2 * (-this.sc);
        var ry3 = rx2 * this.sc + ry2 * this.cc;
        var rz3 = rz2;

        this.mcList[j].cx = rx3;
        this.mcList[j].cy = ry3;
        this.mcList[j].cz = rz3;

        var per = this.d / (this.d + rz3);

        this.mcList[j].x = (this.howElliptical * rx3 * per) - (this.howElliptical * 2);
        this.mcList[j].y = ry3 * per;
        this.mcList[j].scale = per;
        this.mcList[j].alpha = per;

        this.mcList[j].alpha = (this.mcList[j].alpha - 0.6) * (10 / 6);
      }

      this.doPosition();
      this.depthSort();
    },
    depthSort() {
      var i = 0;
      var aTmp = [];

      for (i = 0; i < this.aA.length; i++) {
        aTmp.push(this.aA[i]);
      }

      aTmp.sort
        (
          function (vItem1, vItem2) {
            if (vItem1.cz > vItem2.cz) {
              return -1;
            }
            else if (vItem1.cz < vItem2.cz) {
              return 1;
            }
            else {
              return 0;
            }
          }
        );

      for (i = 0; i < aTmp.length; i++) {
        aTmp[i].style.zIndex = i;
      }
    },
    positionAll() {
      var phi = 0;
      var theta = 0;
      var max = this.mcList.length;
      var i = 0;

      var aTmp = [];
      var oFragment = document.createDocumentFragment();

      //随机排序
      for (i = 0; i < this.aA.length; i++) {
        aTmp.push(this.aA[i]);
      }

      aTmp.sort
        (
          function () {
            return Math.random() < 0.5 ? 1 : -1;
          }
        );

      for (i = 0; i < aTmp.length; i++) {
        oFragment.appendChild(aTmp[i]);
      }

      this.oDiv.appendChild(oFragment);

      for (var i = 1; i < max + 1; i++) {
        if (this.distr) {
          phi = Math.acos(-1 + (2 * i - 1) / max);
          theta = Math.sqrt(max * Math.PI) * phi;
        }
        else {
          phi = Math.random() * (Math.PI);
          theta = Math.random() * (2 * Math.PI);
        }
        //坐标变换
        this.mcList[i - 1].cx = this.radius * Math.cos(theta) * Math.sin(phi);
        this.mcList[i - 1].cy = this.radius * Math.sin(theta) * Math.sin(phi);
        this.mcList[i - 1].cz = this.radius * Math.cos(phi);

        this.aA[i - 1].style.left = this.mcList[i - 1].cx + this.oDiv.offsetWidth / 2 - this.mcList[i - 1].offsetWidth / 2 + 'px';
        this.aA[i - 1].style.top = this.mcList[i - 1].cy + this.oDiv.offsetHeight / 2 - this.mcList[i - 1].offsetHeight / 2 + 'px';
      }
    },
    doPosition() {
      var l = this.oDiv.offsetWidth / 2;
      var t = this.oDiv.offsetHeight / 2;
      for (var i = 0; i < this.mcList.length; i++) {
        this.aA[i].style.left = this.mcList[i].cx + l - this.mcList[i].offsetWidth / 2 + 'px';
        this.aA[i].style.top = this.mcList[i].cy + t - this.mcList[i].offsetHeight / 2 + 'px';
        this.aA[i].style.fontSize = Math.ceil(12 * this.mcList[i].scale / 2) + 8 + 'px';
        this.aA[i].style.filter = 'alpha(opacity=' + 100 * this.mcList[i].alpha + ')';
        this.aA[i].style.opacity = this.mcList[i].alpha;
      }
    },
    sineCosine(a, b, c) {
      let dtr = Math.PI / 180;
      this.sa = Math.sin(a * dtr);
      this.ca = Math.cos(a * dtr);
      this.sb = Math.sin(b * dtr);
      this.cb = Math.cos(b * dtr);
      this.sc = Math.sin(c * dtr);
      this.cc = Math.cos(c * dtr);
    },
    setWords() {
      this.words = [];
      this.wordsContent.forEach((e, i) => {
        let classChoose = this.colorName[Math.floor(Math.random() * this.colorName.length)];
        let wordItem = {
          data: e,
          class: classChoose
        }
        this.words.push(wordItem);
      })
      this.init();
    },
    init() {
      let outer = document.querySelector('.word-wrap');
      let min = outer.clientHeight > outer.clientWidth ? outer.clientWidth : outer.clientHeight;
      this.oDiv = document.getElementById('tagbox');
      this.aA = this.oDiv.getElementsByTagName('a');
      this.d = min * 2 / 5;
      this.mcList = [];
      this.active = false;
      this.lasta = 1;
      this.lastb = 1;
      this.distr = true;
      this.tspeed = 5;
      this.size = min;
      this.mouseX = 0;
      this.mouseY = 0;
      this.howElliptical = 1;
      this.i = 0;
      this.oTag = null;
      this.sa = null;
      this.ca = null;
      this.sb = null;
      this.cb = null;
      this.sc = null;
      this.cc = null;
      // 盒子与圆球的边距
      this.padding = min / 2;
      this.$nextTick(function () {
        this.setCloud();
      })
    }
  },
  watch: {
  },
  computed: {
    ...mapGetters([
    ])
  },
  created() {
  },
  mounted() {
    this.setWords();
    window.addEventListener('resize', () => {
      if (document.querySelector('.word-wrap') && document.getElementById('tagbox') && document.getElementById('tagbox').getElementsByTagName('a')) {
        this.setWords();
      }
    })
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.word-wrap {
  width: 100%;
  height: 100%;
  padding: 0.1rem;
  overflow: hidden;
  #tagbox {
    position: relative;
    width: 100%;
    height: 100%;
    .word-link {
      position: absolute;
      padding: 0.03rem 0.06rem;
      font-family: Microsoft YaHei;
      color: #fff;
      top: 0;
      font-weight: bold;
      text-decoration: none;
      left: 0;
      &:hover {
        border: #eee 1px solid;
        background: #000;
      }
      &.color-blue {
        color: $blue;
      }
      &.color-red {
        color: $red;
      }
      &.color-yellow {
        color: $yellow;
      }
      &.color-green {
        color: $green;
      }
    }
  }
}
</style>