import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/index.vue'
// import Chart from './views/chart.vue'
import TotalProgram from './views/totalProgram.vue'
import TotalPeople from './views/totalPeople.vue'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'index',
      component: Index
    },
    // {
    //   path: '/chart',
    //   name: 'chart',
    //   component: Chart
    // },
    {
      path: '/program',
      name: 'totalProgram',
      component: TotalProgram
    },
    {
      path: '/people',
      name: 'totalPeople',
      component: TotalPeople
    }
  ]
})