<template>
  <div class="people-cloud-wrap">
    <div class="cloud-container" id="main"></div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import echarts from 'echarts/lib/echarts';
import 'echarts-wordcloud/dist/echarts-wordcloud.min.js'
export default {
  components: {
  },
  props: ['nameArr'],
  name: 'peopleCloud',
  data() {
    return {
      value: [],
      colors: ['#44FBDC', '#1296DB', '#0041FB', 'rgba(123, 255, 141, .8)', '#6E4CFC', '#099AAB', '#88EFFF', 'rgba(77, 196, 115, .9)', '#91BDFF', 'rgba(119, 161, 226, .7)', 'rgba(24, 221, 123, .7)'],
      timer: null
    }
  },
  methods: {
    ...mapActions([
    ]),
    setCloud() {
      let that = this;
      var data = {
        value: this.value,
        //小鸟图片
        image: "data:image/png;base64,"
      }
      var myChart = echarts.init(document.getElementById('main'));
      //温馨提示：image 选取有严格要求不可过大；，否则firefox不兼容 iconfont上面的图标可以
      myChart.clear();
      var maskImage = new Image();
      maskImage.src = data.image

      maskImage.onload = function () {
        myChart.setOption({
          tooltip: {
            show: false
          },
          series: [{
            type: 'wordCloud',
            gridSize: 8 * that.fontScale,
            sizeRange: [8 * that.fontScale, 44 * that.fontScale],
            rotationRange: [-90, 90],
            rotationStep: 90,
            drawOutOfBound: true,
            maskImage: maskImage,
            textStyle: {
              normal: {
                color: function () {
                  let index = parseInt(Math.random()*that.colors.length);
                  return that.colors[index];
                }
              }
            },
            left: 'center',
            top: 'center',
            width: '100%',
            height: '100%',
            data: data.value
          }]
        })
      }
    }
  },
  watch: {
  },
  computed: {
    ...mapGetters(['fontScale'
    ])
  },
  created() { },
  mounted() {
    let value = [];
    this.nameArr.forEach((element, i) => {
      let rand = parseInt(Math.random()*60);
      let obj = {};
      obj.name = element;
      obj.value = rand;
      value.push(obj);
      if ( i === this.nameArr.length -1 ) {
        this.value = value;
        this.setCloud();
        clearInterval(this.timer);
        // this.timer = setInterval(() => {
        //   this.setCloud();
        // }, 10000)
      }
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.people-cloud-wrap {
  width: 200%;
  height: 200%;
  transform: scale(.5);
  transform-origin: left top;
  .cloud-container {
    width: 100%;
    height: 100%;
  }
}
</style>