<template>
  <div class="map-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
var echarts = require("echarts");
export default {
  components: {},
  name: "pileupBarChart",
  data() {
    return {
      data: []
    };
  },
  props: ["chartId", "pileupBarChart"],
  methods: {
    ...mapActions([]),
    setChart(datas) {
      console.log(datas);
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.chartId));
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          },
          formatter: "{b0}: {c0}%<br />{b1}: {c1}%",
          textStyle: {
            fontSize: 12 * this.fontScale
          }
        },
        legend: {
          top: 0,
          right: 26 * this.fontScale,
          itemHeight: 6 * this.fontScale,
          itemWidth: 10 * this.fontScale,
          textStyle: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          }
        },
        grid: {
          width: "85%",
          height: "70%",
          top: "15%",
          bottom: "10%"
        },
        color: ["#36DEC1", "#3A78F5"],
        xAxis: {
          type: "category",
          axisLabel: {
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          },
          splitArea: {
            interval: 5
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#21628c"
            }
          },
          data: datas.name,
          axisPointer: {
            type: "shadow"
          }
        },
        yAxis: {
          type: "value",
          axisLine: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: "#13325b"
            }
          },
          axisTick: {
            show: false
          },
          min: 0,
          max: 100,
          interval: 25,
          axisLabel: {
            formatter: "{value}%",
            color: "#90a9ce",
            fontSize: 12 * this.fontScale
          }
        },
        series: [
          {
            name: "男性占有率",
            type: "bar",
            barWidth: "20%",
            stack: "总量",
            data: datas.boy
          },
          {
            name: "女性占有率",
            type: "bar",
            barWidth: "20%",
            stack: "总量",
            data: datas.girl
          }
        ]
      };

      myChart.setOption(option);
    }
  },
  watch: {},
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {
    console.log("pile");
  },
  mounted() {
    let datasMap = {
      boy: [],
      girl: [],
      name: []
    };
    let all = this.pileupBarChart.all;
    this.pileupBarChart.charts.map(item => {
      let total = Math.floor((item.value / all) * 100);
      let boy = Math.floor((item.boy / all) * 100);
      datasMap.boy.push(boy);
      datasMap.girl.push(total - boy);
      datasMap.name.push(item.name);
    });
    this.setChart(datasMap);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";

.map-wrap {
  width: 100%;
  height: 100%;
}
</style>

