<template>
  <div class="waterchart-wrap">
    <canvas
      class="waterbox"
      :style="{width:size+'rem',height:size+'rem'}"
      :id="chartId"
      v-if="!this.static"
    ></canvas>
    <div class="waterbox" :style="{width:size+'rem',height:size+'rem'}" v-else>
      <span class="waterdata">{{this.innerData+'%'}}</span>
      <span class="water-pic" :style="{top:(100 - this.innerData - 10)+'%'}"></span>
    </div>
    <div class="watertitle">{{title}}</div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import radar from "echarts";

export default {
  components: {},
  props: ["innerData", "title", "unit", "size", "total", "chartId", "static"],
  name: "water-echart",
  data() {
    return {
      data: [],
      rangeValue: 0,
      canvas: null,
      ctx: null,
      nowRange: 0, //用于做一个临时的range
      mW: 0,
      mH: 0,
      lineWidth: 1,
      r: null,
      cR: null,
      sX: 0,
      sY: 0,
      axisLength: 0, //轴长
      waveWidth: 0.08, //波浪宽度,数越小越宽
      waveHeight: 5.5, //波浪高度,数越大越高
      speed: 0.06, //波浪速度，数越大速度越快
      xOffset: 0, //波浪x偏移量
      IsdrawCircled: false
    };
  },
  methods: {
    ...mapActions([]),
    initWater(id) {
      this.canvas = document.getElementById(id);
      this.ctx = this.canvas.getContext("2d");

      //画布属性
      this.mW = this.canvas.width = this.size * 100 * this.fontScale;
      this.mH = this.canvas.height = this.size * 100 * this.fontScale;
      console.log(this.mH);
      this.sY = this.mH / 2;
      this.axisLength = this.mW;
      //圆属性
      this.r = this.mH / 2; //圆心
      this.cR = this.r + 1;
      // this.cR = this.r - 16 * this.lineWidth; //圆半径

      this.ctx.lineWidth = this.lineWidth;
      this.render();
    },
    //画sin 曲线函数
    drawSin(xOffset) {
      let that = this;
      that.ctx.save();
      var points = []; //用于存放绘制Sin曲线的点
      that.ctx.beginPath();

      //在整个轴长上取点
      for (
        var x = that.sX;
        x < that.sX + that.axisLength;
        x += 20 / that.axisLength
      ) {
        //此处坐标(x,y)的取点，依靠公式 “振幅高*sin(x*振幅宽 + 振幅偏移量)”
        var y = -Math.sin(
          (that.sX + x) * (that.waveWidth / this.fontScale) + that.xOffset
        );
        var dY = that.mH * (1 - that.nowRange / 100);

        points.push([x, dY + y * that.waveHeight * this.fontScale]);

        that.ctx.lineTo(x, dY + y * that.waveHeight * this.fontScale);
      }
      //封闭路径
      that.ctx.lineTo(that.axisLength, that.mH);
      that.ctx.lineTo(that.sX, that.mH);
      that.ctx.lineTo(points[0][0], points[0][1]);
      var grd = that.ctx.createLinearGradient(0, 0, 180, 0);
      grd.addColorStop(0, "#0EB6FD");
      grd.addColorStop(1, "#01FFFF");
      that.ctx.fillStyle = grd;
      // ctx.fillStyle = '#1c86d1';
      that.ctx.fill();
      that.ctx.restore();
    },
    //画圈函数
    drawCircle() {
      this.ctx.beginPath();
      this.ctx.strokeStyle = "#1080d0";
      this.ctx.arc(this.r, this.r, this.cR + 1, 0, 2 * Math.PI);
      this.ctx.stroke();
      this.ctx.beginPath();
      this.ctx.arc(this.r, this.r, this.cR, 0, 2 * Math.PI);
      this.ctx.clip();
    },
    //写百分比文本函数
    drawText() {
      this.ctx.save();
      const size = 0.5 * this.cR;
      this.ctx.font = size + "px Microsoft Yahei";
      this.ctx.textAlign = "center";
      this.ctx.fillStyle = "#DDE7F5";
      let str = this.unit ? this.unit : "";
      this.ctx.fillText(~~this.nowRange + str, this.r, this.r + size / 2);
      this.ctx.restore();
    },
    render() {
      this.ctx.clearRect(0, 0, this.mW, this.mH);
      if (this.IsdrawCircled == false) {
        this.drawCircle();
      }

      if (this.nowRange <= this.rangeValue) {
        var tmp = 1;
        this.nowRange += tmp;
      }

      if (this.nowRange > this.rangeValue) {
        var tmp = 1;
        this.nowRange -= tmp;
      }

      this.drawSin(this.xOffset);
      this.drawText();
      this.xOffset += this.speed * this.fontScale;
      window.requestAnimationFrame(this.render);
    }
  },
  watch: {},
  computed: {
    ...mapGetters(["fontScale"]),
    percent() {
      return (1 - this.innerData / this.total) * 100;
    }
  },
  created() {},
  mounted() {
    this.rangeValue = this.innerData;
    if (this.static) {
    } else {
      this.initWater(this.chartId);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.waterbox {
  position: relative;
  z-index: 1;
  margin: auto;
  border: 1px solid #1592e6;
  border-radius: 50%;
  overflow: hidden;
}
.water-pic {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(../../../public/images/water_pic.png);
  background-size: cover;
  background-repeat: no-repeat;
}
.waterdata {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  color: #dde7f5;
  font-size: 0.16rem;
  transform: translate(-50%, -50%);
}

.watertitle {
  padding-top: 0.06rem;
  color: #90a9ce;
  font-size: 0.14rem;
}
</style>