  methods: {
    ...mapActions(["SetOnloading"]),
    setChange() {
      let num = Math.floor(Math.random() * 4);
      num = num === 0 ? 1 : num;
      this.num = num;
      for (let i = 0; i < num; i++) {
        let index = Math.floor(Math.random() * this.mapData.length);
        while(this.mapChange.indexOf(this.mapData[index].name) > -1) {
          index = Math.floor(Math.random() * this.mapData.length);
        }
        this.mapChange.push(this.mapData[index].name);
      }
      this.totalPeople += num;
      this.mapChange.forEach(ele => {
        this.mapData.forEach(e => {
          if (e.name === ele) {
            e.value++;
            e.data.program++;
          }
        });
      });
      setTimeout(() => {
        this.mapChange = [];
      }, 3000);

