<template>
  <div class="chart-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
var echarts = require("echarts");
export default {
  components: {},
  props: ["chartId", "chartData", "colors", "config"],
  name: "circleChart",
  data() {
    return {
      total: 0
    };
  },
  methods: {
    ...mapActions([]),
    setChart(datas) {
      // 基于准备好的dom，初始化echarts实例
      let that = this;
      let myChart = echarts.init(document.getElementById(this.chartId));
      let option = {
        title: {
          show: true,
          text:
            "{a|" +
            (typeof this.config.total === "string"
              ? this.config.total
              : this.totalSum(datas)) +
            "}{b|" +
            (this.config.unit ? this.config.unit : "") +
            "}",
          textStyle: {
            rich: {
              a: {
                color: "#fff",
                fontSize: 20 * this.fontScale
              },
              b: {
                color: "#fff",
                fontSize: 12 * this.fontScale
              }
            }
          },
          subtext: this.config.title,
          subtextStyle: {
            color: "#90A9CE",
            fontSize: 12 * this.fontScale
          },
          x: "center",
          y: "32%"
        },
        dataset: {
          source: datas
        },
        tooltip: {
          trigger: "item",
          formatter: function(params) {
            return params.data.name + ":" + params.data.value;
          },
          textStyle: {
            fontSize: 12 * this.fontScale
          }
        },
        color: this.colors,
        legend: {
          orient: "vertical",
          y: "75%",
          height: 40 * this.fontScale,
          itemHeight: 6 * this.fontScale,
          itemWidth: 10 * this.fontScale,
          textStyle: {
            color: "#90A9CE",
            fontSize: 12 * this.fontScale
          },
          formatter: params => {
            let val = "";
            let total = that.totalSum(datas);
            let text = null;
            datas.forEach(item => {
              if (item.name === params) {
                val = ((item.value / total) * 100).toFixed(0) + "%";
              }
            });
            if (that.config.percent) {
              text = params + ":" + val;
            } else {
              text = params;
            }
            return text;
          }
        },
        series: [
          {
            type: "pie",
            center: ["50%", "40%"],
            radius: ["50%", "70%"],
            avoidLabelOverlap: false,
            label: {
              show: false
            },
            labelLine: {
              normal: {
                show: false
              }
            }
          }
        ]
      };
      myChart.setOption(option);
    },
    totalSum(datas) {
      this.total = 0;
      datas.forEach(item => {
        this.total += parseInt(item.value);
      });
      return this.total;
    }
  },
  watch: {
    chartData(newVal) {
      this.setChart(newVal);
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {},
  mounted() {
    this.setChart(this.chartData);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.chart-wrap {
  width: 100%;
  height: 100%;
}
</style>