// import * as types from '../mutation-types'
import api from '@/api/index'
const state = {
  allNumber: 0,
  crfcode: '', // csrf token
  collection: '', // 公钥
  fontScale: 1
}

const getters = {
  allNumber: state => state.allNumber,
  crfcode: state => state.crfcode,
  collection: state => state.collection,
  fontScale: state => state.fontScale,
  userAgent: state => state.userAgent,
  onLoading: state => state.onLoading
}

const actions = {
  getAllNum: ({
    commit
  }, data) => commit('getAllNum', data),
  getLogin({
    commit
  }, data) { // 登录函数
    api.getDistrict(data).then(json => {
      console.log(json)
    })
  },
  AgetCrf({
    commit
  }, inner) { // 获取token
    api.getCsrf().then(json => {
      commit('getCrf', json.data.result)
    })
  },
  AgetCollection({
    commit
  }, inner) { // 获取公钥
    api.getColle().then(json => {
      commit('getCollection', json.data.result)
    })
  },
  SetScale({
    commit
  }, inner) {
    commit('SetScale', inner);
  },
  SetUserAgent({ commit }, inner) {
    commit('setUserAgent', inner)
  },
  SetOnloading({ commit }, inner) {
    commit('setOnloading', inner)
  }
}

const mutations = {
  getAllNum(state, inner) {
    console.log(inner);
  },
  getCrf(state, inner) {
    state.crfcode = ''
    state.crfcode = inner
    console.log(inner)
  },
  getPasWord(state, inner) {
    return inner
  },
  getCollection(state, inner) {
    state.collection = ''
    state.collection = inner
    console.log(inner)
  },
  SetScale(state, inner) {
    state.fontScale = inner;
  },
  setUserAgent(state, inner) {
    state.userAgent = inner;
  },
  setOnloading(state, inner) {
    state.onLoading = inner;
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}