<template>
  <div class="amap-wrap">
    <div id="myEchart"></div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import echarts from "echarts/lib/echarts";
import "echarts/map/js/china.js";
import "echarts/map/js/province/shanxi1.js";

export default {
  components: {},
  name: "amap",
  props: ["mapData", "dataChange"],
  data() {
    return {
      data: [],
      changeData: [],
      province: true,
      max: 0
    };
  },
  methods: {
    ...mapActions([]),
    chinaConfigure(centerVal) {
      let that = this;
      let myChart = echarts.init(document.getElementById("myEchart")); //这里是为了获得容器所在位置
      myChart.clear();
      myChart;
      let option = {
        title: false,
        tooltip: {
          trigger: "item",
          textStyle: {
            fontSize: 12 * this.fontScale
          }
        },
        visualMap: {
          show: false,
          min: 0,
          max: this.max,
          x: "right",
          y: "bottom",
          itemWidth: 10,
          seriesIndex: [0],
          inRange: {
            color: ["transparent", "rgba(97, 181, 255, .8)"]
          },
          calculable: false
        },
        geo: {
          map: "陕西",
          roma: true,
          zoom: 1.2,
          scaleLimit: {
            max: 3,
            min: 0.5
          },
          label: {
            normal: {
              show: true,
              textStyle: {
                fontSize: 12 * this.fontScale,
                color: "#C1C6D6"
              }
            },
            emphasis: {
              show: true,
              textStyle: {
                fontWeight: 'bold',
                fontSize: 18 * this.fontScale,
                color: "#fff"
              }
            }
          },
          itemStyle: {
            normal: {
              borderWidth: 2,
              borderColor: "#5DB7E8"
            },
            emphasis: {
              areaColor: "rgba(97, 181, 255, 1)"
            }
          }
        },
        series: [
          // {
          //   type: "scatter",
          //   coordinateSystem: 'geo',
          //   data: centerVal,
          //   symbolSize: 10 * this.fontScale,
          //   symbol: "circle",
          //   symbolRotate: 35,
          //   symbolOffset: [0, "-150%"],
          //   itemStyle: {
          //     normal: {
          //       color: "#fff"
          //     },
          //     emphasis: {
          //       color: "rgba(64, 255, 255, .3)"
          //     }
          //   },
          //   tooltip: {
          //     show: false
          //   },
          //   animation: false,
          // },
          {
            name: "",
            type: "map",
            geoIndex: 0,
            tooltip: {
              show: true,
              // position: 'left',
              backgroundColor: 'transparent',
              formatter: function (params) {
                let str = '<div class="toolbox"><div class="border-top"></div><div class="border-bottom"></div><div class="tool-title"><span class="tool-icon iconfont iconplace"></span>' + params.name + '</div><div class="tool-lines">立法项目数：' + params.data.data.program + '</div><div class="tool-lines">征集活动数：' + params.data.data.active + '</div><div class="tool-lines">参与人数：' + params.data.data.people + ' 支持率' + params.data.data.support + '%'
                return str;
              }
            },
            data: that.data
          },
          {
            name: "",
            type: "effectScatter",
            coordinateSystem: "geo",
            data: centerVal,
            symbolSize: 10 * this.fontScale,
            symbolOffset: [0, "-150%"],
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke"
            },
            hoverAnimation: true,
            label: {
              show: true,
              position: "right",
              formatter: function (params) {
                return "+1";
              }
            },
            itemStyle: {
              normal: {
                color: "#fff"
              }
            },
            tooltip: { show: false },
            data: that.changeData
          }
        ]
      };
      myChart.setOption(option);
    },
    getCenter(name) {
      let that = this;
      let centerVal = [];
      let data = that.objToStrMap(that.data);
      AMap.plugin(
        ["AMap.DistrictSearch", "AMap.DistrictLayer", "AMap.PolyEditor"],
        function () {
          new AMap.DistrictSearch({
            subdistrict: 1, //返回下一级行政区
            extensions: "all", //返回行政区边界坐标组等具体信息
            level: "city" //查询行政级别为 市
          }).search(name, function (status, result) {
            result.districtList[0].districtList.forEach((e, i) => {
              let value = data.get(e.name);
              value.adcode = e.adcode;
              data.set(e.name, value);
              let obj = {
                name: e.name,
                value: [e.center.lng, e.center.lat, 5]
              };
              centerVal.push(obj);

              if (i === result.districtList[0].districtList.length - 1) {
                that.data = that.strMapToArr(data);
                if (that.dataChange.length > 0) {
                  let change = [];
                  that.dataChange.forEach((ele, index) => {
                    let obj = {};
                    obj.name = ele;
                    obj.value = centerVal.find(function (x) {
                      return x.name === ele;
                    }).value;
                    change.push(obj);
                    if (index === that.dataChange.length - 1) {
                      that.changeData = change;
                      that.chinaConfigure(centerVal);
                    }
                  });
                } else {
                  that.changeData = [];
                  that.chinaConfigure(centerVal);
                }
              }
            });
          });
        }
      );
    },
    objToStrMap(obj) {
      let strMap = new Map();
      for (let k of Object.keys(obj)) {
        strMap.set(obj[k].name, obj[k]);
      }
      return strMap;
    },
    strMapToArr(strMap) {
      let arr = [];
      let i = 0;
      for (let [k, v] of strMap) {
        arr.push(v);
      }
      return arr;
    },
    setData() {
      let data = [];
      let max = 0;
      this.mapData.forEach((e, i) => {
        if (max < e.value) {
          max = e.value;
        }
        let objData = {};
        for (let k of Object.keys(e)) {
          if (k !== "child") {
            objData[k] = e[k];
          }
        }
        data.push(objData);
        if (i === this.mapData.length - 1) {
          this.data = data;
          this.max = max;
          this.getCenter("陕西省");
        }
      });
    }
  },
  watch: {
    mapData() {
      if (this.province) {
        this.setData();
      }
    },
    dataChange() {
      // this.setData();
      this.setData();
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() { },
  mounted() {
    let that = this;
    this.setData();
    this.$nextTick(() => {
      let myChart = echarts.init(document.getElementById("myEchart")); //这里是为了获得容器所在位置
      myChart.on("click", "series.map", function (params) {
        that.province = false;
      });
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.amap-wrap {
  width: 100%;
  height: 100%;
  // background-color: #000079;
  #myEchart {
    width: 100%;
    height: 100%;
  }
}
</style>
<style>
.toolbox {
  position: relative;
  width: auto;
  padding: 0.25rem;
  border-radius: 0;
  color: #000;
  font-size: 0.14rem;
  text-align: left;
  background-color: rgba(13, 21, 45, 0.84);
}
/* .toolbox::after {
  position: absolute;
  right: -.24rem;
  top: 50%;
  transform: translateY(-50%);
  border: .12rem solid transparent;
  border-left-color: rgba(13, 21, 45, .84);
  content: '';
} */
.toolbox .tool-title {
  padding-bottom: 0.17rem;
  font-size: 0.16rem;
  line-height: 0.21rem;
  color: #48f2f8;
}
.toolbox .tool-title .tool-icon {
  padding-right: 0.08rem;
  font-size: 0.14rem;
}
.toolbox .tool-lines {
  padding-bottom: 0.09rem;
  font-size: 0.14rem;
  line-height: 0.19rem;
  color: #dee6ff;
}
.toolbox .tool-lines:last-of-type {
  padding-bottom: 0.02rem;
}
.toolbox .border-top {
  position: absolute;
  left: -1px;
  top: -1px;
  width: 0.47rem;
  height: 0.2rem;
}
.toolbox .border-top::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 2px;
  height: 100%;
  background-color: #48f2f8;
  content: "";
}
.toolbox .border-top::after {
  position: absolute;
  left: 0;
  top: 0;
  height: 2px;
  width: 100%;
  background-color: #48f2f8;
  content: "";
}
.toolbox .border-bottom {
  position: absolute;
  bottom: -1px;
  right: -1px;
  width: 0.8rem;
  height: 0.5rem;
}
.toolbox .border-bottom::before {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 2px;
  height: 100%;
  background-color: #48f2f8;
  content: "";
}
.toolbox .border-bottom::after {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 2px;
  width: 100%;
  background-color: #48f2f8;
  content: "";
}
</style>
