<template>
  <div class="map-wrap" :id="chartId"></div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
var echarts = require("echarts");
export default {
  components: {},
  name: "richbarChart",
  data() {
    return {
      dataMap: {
        all: null,
        charts: {}
      }
    };
  },
  props: ["chartId", "richbarChart", "icons"],
  methods: {
    ...mapActions([]),
    setChart(datas) {
      console.log(datas);
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(this.chartId));
      let that = this;
      let option = {
        tooltip: {
          textStyle: {
            fontSize: 12 * this.fontScale
          }
        },
        xAxis: [
          {
            type: "value",
            max: datas.all,
            splitLine: {
              show: true,
              lineStyle: {
                color: "rgba(255,255,255,0.05)"
              }
            },
            axisTick: false,
            axisLine: false,
            axisLabel: {
              color: "#90A9CE",
              fontSize: 12 * this.fontScale
            },
            z: 2
          }
        ],
        color: "#3FA1FF",
        yAxis: [
          {
            type: "category",
            data: Object.keys(datas.charts),
            axisTick: false,
            axisLine: {
              lineStyle: {
                color: "rgba(255,255,255,0.05)"
              }
            },
            axisLabel: {
              formatter: function(value, index) {
                let len = Object.keys(datas.charts).length;
                let icon = "other";
                let text = len - index;
                if (index === len - 1) {
                  icon = "first";
                  text = "";
                }
                if (index === len - 2) {
                  icon = "second";
                  text = "";
                }
                if (index === len - 3) {
                  icon = "three";
                  text = "";
                }
                if (index === len - 4) {
                  icon = "four";
                  text = "";
                }
                if (index === len - 5) {
                  icon = "five";
                  text = "";
                }
                if (index === len - 6) {
                  icon = "six";
                  text = "";
                }
                if (index === len - 7) {
                  icon = "seven";
                  text = "";
                }
                if (index === len - 8) {
                  icon = "eight";
                  text = "";
                }

                return "{" + icon + "|" + text + " }{value|" + value + "}";
              },
              color: "#90A9CE",
              rich: {
                value: {
                  height: 20 * this.fontScale,
                  width: 40 * this.fontScale,
                  fontSize: 12 * this.fontScale,
                  align: "center",
                  margin: 10,
                },
                second: {
                  height: 21 * this.fontScale,
                  width: 16 * this.fontScale,
                  fontSize: 12 * this.fontScale,
                  align: "center",
                  paddingRight: 10 * this.fontScale,
                  backgroundColor: {
                    image: this.icons.second
                  }
                },
                first: {
                  height: 21 * this.fontScale,
                  width: 16 * this.fontScale,
                  fontSize: 12 * this.fontScale,
                  align: "center",
                  backgroundColor: {
                    image: this.icons.first
                  }
                },
                three: {
                  height: 21 * this.fontScale,
                  width: 16 * this.fontScale,
                  fontSize: 12 * this.fontScale,
                  align: "center",
                  backgroundColor: {
                    image: this.icons.three
                  }
                },
                four: {
                  height: 16 * this.fontScale,
                  width: 16 * this.fontScale,
                  fontSize: 12 * this.fontScale,
                  align: "center",
                  backgroundColor: {
                    image: this.icons.four
                  }
                },
                five: {
                  height: 16 * this.fontScale,
                  width: 16 * this.fontScale,
                  fontSize: 12 * this.fontScale,
                  align: "center",
                  backgroundColor: {
                    image: this.icons.five
                  }
                },
                six: {
                  height: 16 * this.fontScale,
                  width: 16 * this.fontScale,
                  fontSize: 12 * this.fontScale,
                  align: "center",
                  backgroundColor: {
                    image: this.icons.six
                  }
                },
                seven: {
                  height: 16 * this.fontScale,
                  width: 16 * this.fontScale,
                  fontSize: 12 * this.fontScale,
                  align: "center",
                  backgroundColor: {
                    image: this.icons.seven
                  }
                },
                eight: {
                  height: 16 * this.fontScale,
                  width: 16 * this.fontScale,
                  fontSize: 12 * this.fontScale,
                  align: "center",
                  backgroundColor: {
                    image: this.icons.eight
                  }
                },
                other: {
                  height: 16 * this.fontScale,
                  width: 16 * this.fontScale,
                  fontSize: 12 * this.fontScale,
                  lineHeight: 16 * this.fontScale,
                  align: "center",
                  color: "#FFFFFF",
                  backgroundColor: "#408BD4",
                  borderRadius: 10 * this.fontScale
                }
              }
            },
            splitLine: {
              show: false
            }
          },
          {
            type: "category",
            axisLabel: {
              color: "#90A9CE",
              fontSize: 12 * this.fontScale
            },
            axisTick: false,
            axisLine: {
              show: false
            },
            data: Object.values(datas.charts)
          }
        ],
        grid: {
          left: "18%",
          right: "12%",
          bottom: "15%",
          top: "5%"
        },
        series: [
          {
            type: "bar",
            stack: "chart",
            barWidth: "30%",
            data: Object.keys(datas.charts).map(function(key) {
              return datas.charts[key];
            })
          },
          {
            type: "bar",
            stack: "chart",
            silent: true,
            barWidth: "30%",
            itemStyle: {
              normal: {
                color: "rgba(255,255,255,0.05)"
              }
            },
            data: Object.keys(datas.charts).map(function(key) {
              return datas.all - datas.charts[key];
            }),
            z: 1
          }
        ]
      };

      myChart.setOption(option);
    },
    sortdata(datas) {
      console.log(datas);
      let sortdata = datas.charts.sort(function(a, b) {
        return a.value - b.value;
      });
      this.dataMap.all = datas.all;
      sortdata.forEach(element => {
        this.dataMap.charts[element.name] = element.value;
      });
    }
  },
  watch: {
    linebarChart: function(newVal) {
      this.sortdata(newVal);
      this.setChart(this.dataMap);
    }
  },
  computed: {
    ...mapGetters(["fontScale"])
  },
  created() {},
  mounted() {
    console.log(this.richbarChart);

    this.sortdata(this.richbarChart);
    this.setChart(this.dataMap);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";

.map-wrap {
  width: 100%;
  height: 100%;
}
</style>

