<template>
  <dir class="num-box-wrapper">
    <div class="brand-min hide-min" v-for="(item, index) in totalPeopleArr" :key="index" >
      <div class="now-brand brand-min" :class="changeNum[index]?'active': ''">
        <div :class="userAgent === 'Firefox'?'firefox-min':'brand-content'">{{item}}</div>
      </div>
      <div class="before-brand brand-min" :class="changeNum[index]?'active': ''">
        <div :class="userAgent === 'Firefox'?'firefox-min':'brand-content'">{{lastNumArr[index]}}</div>
      </div>
    </div>
  </dir>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  components: {
  },
  props: ['totalPeople', 'num'],
  name: 'numBox',
  data() {
    return {
      totalPeopleArr: [],
      lastNum: 0,
      changeNum: [],
      lastNumArr: []
    }
  },
  methods: {
    ...mapActions([
    ]),
    getNum(length) {
      let arr = [];
      for (let i = 2; i >= 1; i--) {
        let last = this.lastNum % (Math.pow(10,i));
        let sum = Math.pow(10,i) - last;
        if (this.num >= sum) {
          let changeLength = length + 1 - i;
          for (let j = 0; j < length; j++) {
            arr.push(true);
          }
          return arr;
        }
        arr.push(false);
      }
      return arr;
    },
    getLastArr(val) {
      let str = val + '';
      let length = str.length;
      for (let i = 0; i < length; i++) {
        this.lastNumArr[i] = '' + str.substr(i, 1);
      }
    },
    selectChange() {
      this.lastNum = +this.totalPeopleArr.join('') - this.num;
      this.getLastArr(this.lastNum);
      let length = this.totalPeopleArr.length;
      this.changeNum = this.getNum(length);
      if (this.changeNum.length < length) {
        this.changeNum.push(true);
      }
      setTimeout(() => {
        this.changeNum = [];
      }, 1500)
    },
    setArr(val) {
      let str = val + '';
      let length = str.length;
      for (let i = 0; i < length; i++) {
        this.totalPeopleArr[i] = '' + str.substr(i, 1);
      }
      this.selectChange()
    }
  },
  watch: {
    totalPeople(val) {
      this.setArr(val);
    }
  },
  computed: {
    ...mapGetters(['userAgent'
    ])
  },
  created() {
    this.setArr(this.totalPeople);
  },
  mounted() {
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.num-box-wrapper {
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  height: 100%;
  justify-content: center;
  .before-brand {
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    transform: rotateX(90deg);
    &.active {
      transform: rotateX(0deg);
      opacity: 1;
      animation: hidebefore 0.8s linear;
      animation-fill-mode: forwards;
    }
    @keyframes hidebefore {
      0% {
        transform: rotateX(0deg);
      }
      100% {
        transform: rotateX(90deg);
      }
    }
  }
  .now-brand {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    &.active {
      transform: rotateX(-90deg);
      animation: shownow 0.8s linear;
      animation-delay: 0.8s;
      animation-fill-mode: forwards;
    }
    @keyframes shownow {
      0% {
        transform: rotateX(-90deg);
      }
      100% {
        transform: rotateX(0deg);
      }
    }
  }
  .brand-min.hide-min {
    &::before,
    &::after {
      background-color: transparent;
    }
  }
}
</style>